.contact{
    height: 50vh;
    margin:8rem 1.2rem;
}
.contact-container{
    margin-top: 3rem;
    height: 350px;
    background: rgba(255, 255, 255, 0.05);
    display: flex;
}
.contact-info{
    width: 45%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.info{
    margin: 1rem 3rem;
    display: flex;
    align-items: center;
}
.icon{
    color: var(--primary-color);
}
.info span{
    color: var(--text-color);
    margin-left: 2.5rem;
    letter-spacing: 1px;
}

.info .phno{
    width: 150px;
}
.contact-form{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btnHi{
    color: var(--primary-color);
    padding: 1rem 2rem;
    letter-spacing: 2px;
    border-radius: 5px;
    text-decoration: none;
    background: var(--title-color);
    transition: opacity 300ms ease-in-out;
}
.btnHi:hover,
.btnHi:active{
    opacity: 0.7;
}

@media only screen and (max-width: 720px){
    .contact{
        margin: 8rem 0;
    }
    .contact-container{
        flex-direction: column;
        align-items: center;
    }
    .info {
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-left: -5rem;
    }
    .btnHi{
        margin-bottom: 2rem;
    }

    .info span{
        margin-left: 2rem;
        font-size: .8rem;
    }

    .btnHi{
        font-size: .8rem;
        padding: 1rem 1rem;
    }
}
